*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  user-select: none;
}

body {
  background-color: #fff;
  font-family: "TT Norms";
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  padding: 0;
  margin: 0;
  border: 0;
  border: none;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
}

.containers {
  max-width: 1440px;
  padding: 0;
  margin: 0 auto;
  width: 100%;
}

ol,
ul {
  padding-left: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

.display-n {
  display: none !important;
}

.opacity {
  opacity: 0;
}

.ind-header {
  width: 100%;
  height: 100vh;
  position: relative;
  background: url(../img/index/banner.png) no-repeat;
  background-size: cover;
  background-position: 0 10%;
  font-family: "TT Norms";
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.ind-header .containers {
  max-width: unset;
  height: 100%;
}

.ind-header__menu {
  position: absolute;
  top: 0;
  right: 0;
  width: 90px;
  height: 100%;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 0 0 32px 0;
  z-index: 5;
}

.ind-header__menu-burger {
  width: 90px;
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.ind-header__menu-language {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 50px;
}

.ind-header__menu-language-ru {
  font-family: TT Norms;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 46px;
  color: #002c5b;
  opacity: .5;
  cursor: pointer;
}

.ind-header__menu-language-en {
  font-family: TT Norms;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 46px;
  color: #002c5b;
  opacity: .5;
  cursor: pointer;
}

.ind-header__menu-language-active {
  opacity: 1;
  cursor: pointer;
}

.ind-header__nav {
  width: 100%;
  height: 90px;
  background: #003169;
  position: absolute;
  top: 0;
  left: 0;
}

.ind-header__nav::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #003169;
  opacity: .7;
  z-index: 1;
}

.ind-header__nav-wrapper {
  width: 100%;
  height: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 90px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  z-index: 2;
}

.ind-header__nav-logo {
  margin: -20px 181px 0 0;
  z-index: 5;
}

.ind-header__nav-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 550px;
  margin: 0 auto 0 0;
  z-index: 5;
}

.ind-header__nav-list-items {
  font-family: TT Norms;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  text-transform: uppercase;
  color: #fff;
  opacity: .6;
  cursor: pointer;
  transition: all .5s ease;
}

.ind-header__nav-list-items:nth-child(2) {
  opacity: 1;
}

.ind-header__nav-list-items:hover {
  transition: all .5s ease;
  opacity: 1;
}

.ind-header__info {
  padding: 90px 0 0 0;
  height: 100%;
  width: 720px;
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: flex-start;
  justify-content: flex-start;
}

.ind-header__info-description {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  transition: all .5s ease;
}

.ind-header__info-description-block {
  width: 389px;
  height: 380px;
  position: relative;
  padding: 89px 0 0 85px;
  z-index: 1;
  position: relative;
}

.ind-header__info-description-block-wrapper {
  z-index: 5;
  position: absolute;
  top: 85px;
  left: 89px;
}

.ind-header__info-description-block::after {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  opacity: .7;
  background: #003169;
}

.ind-header__info-description-block-title {
  z-index: 5;
  font-family: TT Norms;
  font-style: normal;
  font-weight: bold;
  font-size: 23px;
  line-height: 27px;
  text-transform: uppercase;
  color: #fff;
}

.ind-header__info-description-block-text {
  margin: 20px 0 0 0;
  width: 277px;
  font-family: TT Norms;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  color: #d2d2d2;
}

.ind-header__info-description-block-btn {
  width: 154px;
  height: unset;
  position: relative;
  padding: 45px 0 0 0;
  overflow: hidden;
  background: transparent;
  cursor: pointer;
}

.ind-header__info-description-block-btn::after {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 0;
  width: 100%;
  height: 0;
  border-bottom: 1px solid #fff;
}

.ind-header__info-description-block-btn a {
  font-family: TT Norms;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #fff;
}

.ind-header__info-description-add {
  width: 331px;
  height: 380px;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 27px 0 0 0;
  position: relative;
}

.ind-header__info-description-add-title {
  font-family: TT Norms;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  text-transform: uppercase;
  color: #002c5b;
}

.ind-header__info-description-add-banner {
  margin: 49px 0 0 0;
  text-align: center;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: TT Norms;
  font-style: normal;
  font-weight: bold;
  font-size: 70px;
  line-height: 83px;
  text-transform: uppercase;
  color: #03366e;
  transition: all .5s ease;
  width: 160px;
  height: 160px;
  z-index: 5;
  position: relative;
  background: #fff;
  box-shadow: 0px 0px 20px rgba(0,0,0,.15);
}

.ind-header__info-description-add-banner::after {
  content: "";
  position: absolute;
  border: 2px solid rgba(3,54,109,.09);
  left: -25px;
  right: -25px;
  top: -25px;
  bottom: -25px;
  z-index: 2;
  border-radius: 50%;
  animation: pulse 1.8s linear infinite;
  opacity: 0;
  animation-delay: .5s;
}

.ind-header__info-description-add-banner::before {
  content: "";
  position: absolute;
  border: 2px solid rgba(84,120,158,.09);
  left: -25px;
  right: -25px;
  top: -25px;
  bottom: -25px;
  z-index: 2;
  border-radius: 50%;
  animation: pulse 1.8s linear infinite;
  opacity: 0;
}

.ind-header__info-description-add-slider {
  margin: 50px 0 0 0;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.ind-header__info-description-add-slider-prev {
  position: absolute;
  top: 0;
  left: -65px;
  cursor: pointer;
}

.ind-header__info-description-add-slider-next {
  position: absolute;
  top: 0;
  right: -65px;
  cursor: pointer;
}

.ind-header__info-description-add-slider h3 {
  margin: 10px 0 0 0;
  font-family: TT Norms;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  text-transform: uppercase;
  color: #003169;
}

.ind-header__info-description-add-slider p {
  margin: 5px 0 0 0;
  font-family: TT Norms;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #003169;
}

.ind-header__info-description-add-arrow {
  position: absolute;
  cursor: pointer;
  left: 50%;
  transform: translate(-50%, 0%);
  bottom: 0;
}

.ind-header__info-tabs {
  width: 100%;
  height: 224px;
  position: relative;
  padding: 0 0 0 85px;
  transition: all .5s ease;
}

.ind-header__info-tabs::before {
  content: "";
  background: #002349;
  opacity: .5;
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.ind-header__info-tabs-list {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 662px;
}

.ind-header__info-tabs-items {
  width: 100%;
  height: 224px;
  background: transparent;
  z-index: 5;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
}

.ind-header__info-tabs-items-title {
  margin: 23px 0 0 0;
  font-family: TT Norms;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
  text-align: center;
  color: #fff;
  width: 50px;
}

.ind-header__info-tabs-items-active {
  background: #003169;
}

.ind-header__info-line {
  width: 100%;
  height: 110px;
  overflow: hidden;
  position: relative;
  transition: all .5s ease;
}

.ind-header__info-line::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: #003169;
  opacity: .7;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.ind-header__info-line-wrapper {
  width: 100%;
  position: relative;
  height: 100%;
}

.ind-header__info-line-text {
  position: absolute;
  z-index: 5;
  top: 50%;
  transform: translate(0%, -50%);
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 5;
  left: -20%;
  display: flex;
  width: 420%;
  animation: line 50s infinite linear;
}

.ind-header__info-line-text p {
  font-family: TT Norms;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 19px;
  width: 420%;
  color: #fff;
}

.ind-header__info-line-text p span {
  font-family: TT Norms;
  font-size: 18px;
  font-weight: 400;
  font-style: normal;
  line-height: 26px;
  width: 10%;
  letter-spacing: -0.5px;
  color: #fff;
}

.ind-header__info-play {
  position: absolute;
  right: -250px;
  bottom: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.ind-header__info-play-icons {
  margin: 0 17px 0 0;
}

.ind-header__info-play-title {
  font-family: TT Norms;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 46px;
  color: #fff;
}

@keyframes pulse {
  0% {
